          <p class='title p-item-title'>石品私房mz27 实价不议 玛瑙摆件 澳洲蛋白玛瑙俏色玛瑙鳄鱼摆件</p>
          <div class="cnxh-item-b">
            <p class='cnxh-item-b-l'>￥<span class='big'>1980</span></p>
            <p class='cnxh-item-b-r'>销量 903 件</p>
          </div>
          <!-- 绝对定位bar -->
          <div class="cnxh-item-ab">
            <p class='item-ab-title'><i class="fa fa-heart" aria-hidden="true"></i> 找相似</p>
            <span>发现更多相似的宝贝 <i class='el-icon-arrow-right'></i></span>
          </div>
        </router-link>
      </div>
    </div>
  </div>
</template>

<script>
export default {
  name: 'userHome',
  data () {
    return {
      
    }
  },
  computed: {
    
  },
  created() {
    
  },
  mounted() {
  },
  methods: {
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../../assets/css/public.scss';
.cnxh {
  width: 100%;
  margin-top: 10px;
  height: 701px;
  .wrap {
    background: #fff;
    box-sizing: border-box;
    // head
    .cnxh-title {
      padding-bottom: 16px;
      p {
        border-color: #ff9f00;
        color: #ff9f00;
      }
    }
    // list
    .cnxh-list {
      width: 100%;
      display: flex;
      justify-content: space-between;
      flex-wrap: wrap;
      .cnxh-item {
        position: relative;
        box-sizing: border-box;
        display: block;
        flex: 0 0 20%;
        padding: 20px;
        border: 1px solid #fff;
        border-right-color: $background;
        border-bottom-color: $background;
        // 图片
        .cnxh-item-img {
          width: 100%;
          background: $color-img;
          img {
            display: block;
            width: 100%;
            opacity: .9;
            transition: .3s;
          }
          &:hover img {
            opacity: .7;
          }
        }
        // title
        .title {
          margin-top: 8px;
          height: 34px;
          overflow: hidden;
          text-align: justify;
        }
        .cnxh-item-b {
          margin-top: 26px;
          display: flex;
          justify-content: space-between;
          .cnxh-item-b-l {
            color: $color-red;
            .big {
              font-size: 16px;
            }
          }
          .cnxh-item-b-r {
            margin-top: 3px;
          }
        }
        &:nth-child(5),&:nth-child(10) {
          border-right-color: #fff;
        }
        // 绝对定位
        .cnxh-item-ab {
          transition: .5s;
          // display: none;
          opacity: 0;
          position: absolute;
          z-index: 20;
          left: 0px;
          bottom: 0px;
          width: 100%;
          height: 100px;
          color: #fff;
          text-align: center;
          background: $color-red;
          p {
            width: 150px;
            line-height: 30px;
            height: 30px;
            border-bottom: 1px solid #fff;
            font-size: 14px;
            margin: 18px auto 6px;
          }
          span {
            display: block;
            font-size: 12px;
          }
        }
        &:hover {
          border-color: $color-red;
          .title {
            color: $color-red;
          }
          .cnxh-item-ab {
            // display: block;
            opacity: 1;
          }
        }
      }
    }
  }
}

</style>



