<template>
  <div class="login">
    <div class="header wrap">
      <router-link to='/' class="header-logo"><img src="../assets/logo.png" alt=""></router-link>
      <p>为确保您账户的安全及正常使用，依《网络安全法》相关要求，6月1日起会员账户需绑定手机。如您还未绑定，请尽快完成，感谢您的理解及支持！ </p>
      <router-link to='' class='header-advise'><i class="fa fa-commenting" aria-hidden="true"></i> "登录页面"改进建议</router-link>
    </div>
    <!-- banner -->
    <div class="login-banner">
      <div class="wrap">
        <div class="login-form">
          <!-- 切换按钮 -->
          <div class="form-ab">
            <div class="desc">
              <p v-if='loginType'><i class="fa fa-shield" aria-hidden="true"></i>密码登录在这里<img src="../assets/image/login/login-ab-right.png" alt=""></p>
              <p v-else><i class="fa fa-shield" aria-hidden="true"></i>扫码登录更安全<img src="../assets/image/login/login-ab-right.png" alt=""></p>
            </div>
            <div class="form-ab-img" @click='loginType = !loginType'>
              <img v-if='loginType' src="../assets/image/login/login-type-1.png" alt="">
              <img v-else src="../assets/image/login/login-type-0.png" alt="">
            </div>
            
          </div>
          <!-- 扫码登录 -->
          <div v-if='loginType' class='login-form-con'>
            <div class="title">手机扫码，安全登录</div>
            <div class="qr-bar">
              <div v-show='qrState != 2' class="qr-ab">
                <!-- 失效0 -->
                <div v-show='qrState === 0' class="state-0">
                  <p>二维码已失效</p>
                  <span @click='getQr'>请点击刷新</span>
                </div>
                <!-- 请稍后 -->
                <div v-show='qrState === 1' class="state-1">
                  <i class='el-icon-loading'></i>
                </div>
                <!-- <div v-show='qrState === 1' class="state-1">
                  <i class='el-icon-loading'></i>
                </div> -->
              </div>
              <img src="../assets/image/index/ggs-qr.png" alt="">
            </div>
            <!-- 提示 -->
            <div class="prompt">
              <i class="fa fa-qrcode" aria-hidden="true"></i>
              <p>
                打开 <router-link to=''>手机淘宝</router-link> | <router-link to=''>手机天猫</router-link><br/>
                扫一扫登录
              </p>
            </div>
          </div>
          <!-- 密码登录 -->
          <div v-else class='login-form-con'>
            <div v-show='errMsg.code' class="login-err">
              <i class="fa fa-exclamation-triangle" aria-hidden="true"></i>
              <span v-if='errMsg.code === 3'>你输入的密码和账户名不匹配，是否 <router-link to=''>忘记密码</router-link><br/> 或 <router-link to=''>忘记会员名</router-link></span>
              <span v-else>{{errMsg.msg}}</span>
            </div>
            <div class="title">密码登录</div>
            <!-- 登陆框 -->
            <div class="form-bar">
              <div class="item">
                <span><i class="fa fa-user" aria-hidden="true"></i></span>
                <input @keydown.13='subFunc' v-model='userForm.userName' type="text" placeholder='会员名/邮箱/手机号'>
              </div>
              <div class="item">
                <span><i class="fa fa-lock" aria-hidden="true"></i></span>
                <input @keydown.13='subFunc' v-model='userForm.password' type="password" placeholder='密码'>
              </div>
              <button @click='subFunc' @keydown.13='subFunc' class='form-bar-btn'>登录</button>
              <!-- 登录方式 -->
              <div class="form-login-type">
                <router-link to=''><i class="fa fa-weibo" aria-hidden="true"></i> <span>微博登录</span></router-link>
                <router-link to=''><i class="fa fa-weixin" aria-hidden="true"></i> <span>微信登录</span></router-link>
              </div>
            </div>
          </div>
          <!-- bottom -->
          <div class="bottom">
            <router-link v-if='!loginType' to=''>忘记密码</router-link>
            <router-link v-if='!loginType' to=''>忘记会员名</router-link>

            <router-link v-if='loginType' to=''>密码登录</router-link>
            <router-link to='/register'>免费注册</router-link>
          </div>
        </div>
      </div>
      <footLogin />
    </div>
  </div>
</template>

<script>
import footLogin from '@/components/footer/footLogin'

import { mapGetters, mapActions, mapMutations } from 'vuex'
import login from '@/api/login'
export default {
  name: 'index',
  components: {
    footLogin
  },
  data () {
    return {
      loginType: true, // 二维码登录
      qrState: 1, // 0失效，1请稍后，2请求成功
      // 表单数据
      userForm: {
        userName: '',
        password: ''
      },
      errMsg: {
        code: 0, // 0登录成功，1用户名错误，2，密码错误，3账号没有找到
        msg: ''
      }
    }
  },
  computed: {
    
  },
  created() {
  },
  mounted() {
    this.mockQr();
    // 获取路由参数
    let no = this.$route.query.no
    if (no) {
      this.loginType = false
      this.userForm.userName = no
    }
  },
  methods: {
    ...mapMutations(['SET_USERNAME']),
    // 模拟验证码失效
    mockQr() {
      setTimeout(() => {
        this.qrState = 2;
      }, 2000)
      setTimeout(() => {
        this.qrState = 0;
      }, 4000)
    },

    getQr() {
      this.qrState = 1
      this.mockQr();
    },
    // 登录
    subFunc() {
      let userName = $.trim(this.userForm.userName)
      let password = $.trim(this.userForm.password)
      if (!userName) {
        this.errMsg = {
          code: 1,
          msg: '请输入账户名和密码'
        }
        return false;      
      }
      if (!password) {
        this.errMsg = {
          code: 2,
          msg: '请输入密码'
        }
        return false;
      }
      this.errMsg = '';
      login(this.userForm).then(res => {
        this.$message.success(res.data.msg)
        // userName: '',
        // password: ''
        this.SET_USERNAME(this.userForm.userName)
        this.$cookie.set('userName', this.userForm.userName)
        this.$router.push('/')
      }).catch(str => {
        if (str === '用户名不存在') {
          this.errMsg = {
            code: 2,
            msg: '用户名不存在'
          }
        } else if (str === '用户名或密码错误') {
          this.errMsg = {
            code: 3,
            msg: ''
          }
        }
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../assets/css/public.scss';
.login {
  width: 100%;
  background: #fff;
  // header
  .header {
    height: 88px;
    padding: 20px 0;
    box-sizing: border-box;
    .header-logo {
      display: inline-block;
      margin-top: 3px;
      img {
        height: 42px;
      }
    }
    p {
      float: right;
      padding: 6px 40px;
      background: #fef2f2;
      border: 1px solid #ffb4a8;
    }
    .header-advise {
      float: right;
      margin-top: -4px;
      margin-left: 200px;
      i {
        color: $color-orange;
        font-size: 13px;
      }
      &:hover {
        color: $color-orange;
      }
    }
  }
  // banner
  .login-banner {
    height: 600px;
    background: url('../assets/image/login/login-bg.jpg') top center no-repeat;
    .wrap {
      display: flex;
      justify-content: flex-end;
      align-items: center;
      height: 600px;
    }
    .login-form {
      position: relative;
      width: 350px;
      height: 350px;
      background: rgba(255,255,255,.85);
      box-sizing: border-box;
      padding: 20px;
      .title {
        font-size: 16px;
        margin-top: 18px;
        font-weight: bold;
      }
      .login-form-con {
        height: 252px;
        // background: #ccc;
        // 二维码登录
        // 二维码
        .qr-bar {
          height: 140px;
          width: 140px;
          margin: 30px auto;
          position: relative;
          img {
            width: 140px;
            height: 140px;
          }
          .qr-ab {
            background: rgba(255,255,255,.95);
            position: absolute;
            z-index: 99;
            top: 0px;
            left: 0px;
            width: 140px;
            height: 140px;
            // 失效0
            .state-0 {
              p {
                margin-top: 44px;
                color: #000;
                font-size: 12px;
                text-align: center;
              }
              span {
                cursor: pointer;
                display: block;
                margin: 10px auto 0px;
                width: 100px;
                line-height: 30px;
                height: 30px;
                color: #fff;
                text-align: center;
                border-radius: 3px;
                background: $color-red;
                &:hover {
                  background: red;
                }
              }
            }
            // 请稍后1
            .state-1 {
              line-height: 144px;
              width: 144px;
              text-align: center;
              font-size: 50px;
            }
          }
        }
        // 提示
        .prompt {
          display: flex;
          justify-content: space-between;
          width: 167px;
          margin: 0 auto;
          i {
            font-size: 30px;
            color: $color-orange;
          }
          p {
            line-height: 1.2;
            a {
              color: $color-orange;
            }
          }
        }
        // 密码登录
        .form-bar {
          margin-top: 40px;
          .item {
            margin-bottom: 20px;
            position: relative;
            span {
              position: absolute;
              top: 0px;
              left: 0px;
              display: inline-block;
              width: 40px;
              height: 36px;
              line-height: 36px;
              background: #ccc;
              text-align: center;
              i {
                font-size: 18px;
                color: #fff;
              }
            }
            input {
              display: inline-block;
              box-sizing: border-box;
              border: none;
              width: 100%;
              height: 36px;
              line-height: 36px;
              padding-left: 46px;
              font-size: 14px;
              &:focus {
                box-shadow: 0 0 5px $color-red;
              }
            }
          }
          .form-bar-btn {
            width: 100%;
            height: 36px;
            line-height: 36px;
            text-align: center;
            color: #fff;
            background: $color-red;
            border-radius: 3px;
          }
          .form-login-type {
            display: flex;
            justify-content: flex-start;
            margin-top: 20px;
            a {
              display: inline-block;
              margin-right: 20px;
              i {
                font-size: 14px;
              }
              .fa-weibo {
                color: $color-red;
              }
              .fa-weixin {
                color: #62b900;
              }
              &:hover {
                span {
                  color: $color-red;
                }
              }
            }
          }
        }
        
      }
      
      // bottom
      .bottom {
        margin-top: 20px;
        display: flex;
        justify-content: flex-end;
        a {
          display: inline-block;
          margin-left: 10px;
          &:hover {
            color: $color-orange;
          }
        }
      }
      // 绝对定位
      .form-ab {
        position: absolute;
        right: 6px;
        top: 6px;
        display: flex;
        justify-content: flex-end;
        width: 300px;
        .form-ab-img {
          width: 52px;
          height: 52px;
          cursor: pointer;
          img {
            width: 52px;
            height: 52px;
          }
        }
        .desc {
          position: relative;
          line-height: 24px;
          height: 24px;
          padding: 0 20px 0 18px;
          border: 1px solid #f3d995;
          background: #fefcee;
          color: #999;
          i {
            color: #df9c1f;
            display: inline-block;
            margin-right: 8px;
            font-size: 16px;
            vertical-align: middle;
          }
          img {
            position: absolute;
            height: 14px;
            right: -8px;
            top: 7px;
          }
        }

      }
      .login-err {
        position: absolute;
        right: 20px;
        top: 38px;
        width: 310px;
        box-sizing: border-box;
        border: 1px solid #ffb4a8;
        background: #fef2f2;
        padding: 10px 10px;
        i {
          color: $color-orange;
        }
        span {
          float: right;
          width: 268px;
        }
      }

    }
  }
}
</style>



