import fetch from '@/utils/fetch'

export function getClassify(data) {
  return fetch({
    url: '/getClassify',
    method: 'post',
    data
  })
}

// 有好货
export function getGgs() {
  return fetch({
    url: '/getGgs',
    method: 'get'
  })
}

