export default {
  phoneReg: /^[1][3,4,5,7,8][0-9]{9}$/, // 手机号验证
  gdPhoneReg: /^0\d{2,3}-[1-9]\d{6,7}$/, // 固定电话验证 请使用'-'连接
  QQReg: /^[1-9][0-9]{4,}$/, // qq号
  emailReg: /^[a-z0-9]+([._\\-]*[a-z0-9])*@([a-z0-9]+[-a-z0-9]*[a-z0-9]+.){1,63}[a-z0-9]+$/, // 邮箱
  yzReg: /^[1-9][0-9]{5}$/, // 邮政编码
  // czReg: /^(\d{3,4}-)?\d{7,8}$/, // 传真
  yhReg: /^(\d{16}|\d{19})$/, // 银行账号
  sfzhReg: /(^\d{15}$)|(^\d{18}$)|(^\d{17}(\d|X|x)$)/, // 身份证号
  xydmReg: /[^_IOZSVa-z\W]{2}\d{6}[^_IOZSVa-z\W]{10}$/g, // 信用代码
  cphReg: /^[京津沪渝冀豫云辽黑湘皖鲁新苏浙赣鄂桂甘晋蒙陕吉闽贵粤青藏川宁琼使领A-Z]{1}[A-Z]{1}[A-Z0-9]{4}[A-Z0-9挂学警港澳]{1}$/
}



