const getters = {
  userName: state => state.user.userName,
  userRole: state => state.user.userRole,
  avator: state => state.user.avator,

  permission_routers: state => state.permission.routers,
  addRouters: state => state.permission.addRouters,

  // app
  winScroll: state => state.app.winScroll,
  webPlace: state => state.app.webPlace,
  headNav: state => state.app.headNav,

  // register
  regState: state => state.register.regState,
  dragState: state => state.register.dragState,
  regPhone: state => state.register.regPhone
  
}

export default getters



