<template>
  <div class="register">
    <headIndexLogin />
    
    <div class="wrap">
      <div class='reg-head'>
        <router-link class='reg-logo' to='/'><img src="@/assets/logo.png" alt=""></router-link>
        <span>用户注册</span>
      </div>
      <!-- 导航 -->
      <div class="reg-nav">
        <div class="item active">
          <span class='count'>1</span>
          <span class='title'>设置用户名</span>
        </div>
        <div class="item">
          <span class='count'>2</span>
          <span class='title'>填写账号信息</span>
        </div>
        <div class="item">
          <span class='count'>3</span>
          <span class='title'>设置支付方式</span>
        </div>
        <div class="item">
          <span class='count'>4</span>
          <span class='title'>注册成功</span>
        </div>
      </div>
      <!-- 注册内容显示区域 -->
      <div class="reg-con">
        <div class="reg-con-item">
          <components :is='componentNames[regState]'></components>
          <!-- <reg1SetName /> -->
        </div>
      </div>
    </div>
    <footLogin />
    
    <!-- 注册协议 -->
    <div v-if='showAgree' class="register-agree">
      <div class="box">
        <div class="title">
          <p>注册协议</p>
          <router-link to='/'><i class='fa fa-close'></i></router-link>
        </div>
        <div class="con">
          <p><span class='bold'>【审慎阅读】</span>您在申请注册流程中点击同意前，应当认真阅读以下协议。<span class='bold under'>请您务必审慎阅读、充分理解协议中相关条款内容，其中包括：</span></p>
          <p>1、<span class='bold under'>与您约定免除或限制责任的条款；</span></p>
          <p>2、<span class='bold under'>与您约定法律适用和管辖的条款；</span></p>
          <p>3、<span class='bold under'>其他以粗体下划线标识的重要条款。</span></p>

          <p>如您对协议有任何疑问，可向平台客服咨询。</p>

          <p><span class='bold'>【特别提示】</span>当您按照注册页面提示填写信息、阅读并同意协议且完成全部注册程序后，即表示您已充分阅读、理解并接受协议的全部内容。如您因平台服务与淘宝发生争议的，适用《淘宝平台服务协议》处理。如您在使用平台服务过程中与其他用户发生争议的，依您与其他用户达成的协议处理。</p>
          <p><span class='bold under'>阅读协议的过程中，如果您不同意相关协议或其中任何条款约定，您应立即停止注册程序。</span></p>
          <router-link to=''>《淘宝平台服务协议》</router-link><br/>
          <router-link to=''>《法律声明及隐私权政策》</router-link><br/>
          <router-link to=''>《支付宝服务协议》</router-link>
        </div>
        <button class='btn' @click='hideAgree'>同意注册</button>
      </div>
      
    </div>
    
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
import headIndexLogin from '@/components/header/headIndexLogin'
import footLogin from '@/components/footer/footLogin'

import reg1SetName from './reg1SetName'
import reg2GetCode from './reg2GetCode'
export default {
  name: 'register',
  components: {
    headIndexLogin,
    footLogin,
    // register tabs
    reg1SetName,
    reg2GetCode
  },
  data () {
    return {
      componentNames: ['reg1SetName', 'reg2GetCode'],
      showAgree: false,
      viewBar: footLogin
    }
  },
  computed: {
    ...mapGetters(['regState'])
  },
  created() {
    
  },
  mounted() {
  },
  methods: {
    hideAgree() {
      this.showAgree = false
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '@/assets/css/public.scss';
.register {
  width: 100%;
  background: #fff;
  .wrap {
    // logo
    .reg-head {
      display: flex;
      justify-content: flex-start;
      margin: 20px 0;
      .reg-logo {
        margin-right: 12px;
        line-height: 42px;
        img {
          height: 42px;
        }
      }
      span {
        display: inline-block;
        font-size: 20px;
        margin-top: 10px;
      }
    }

    // nav
    .reg-nav {
      height: 40px;
      border-bottom: 2px solid #e6e6e6;
      display: flex;
      justify-content: center;
      // box-sizing: border-box;
      .item {
        width: 180px;
        height: 40px;
        border-bottom: 2px solid #e6e6e6;
        text-align: center;
        span {
          display: inline-block;
          margin-top: 4px;
          line-height: 24px;
          height: 24px;
          font-size: 14px;
          font-weight: bold;
          color: #999;
        }
        .count {
          width: 24px;
          border-radius: 12px;
          background: #e6e6e6;
          margin-right: 4px;
          text-align: center;
          color: #fff;
          
        }
        &.active {
          border-bottom: 2px solid $color-orange;
          .title {
            color: #333;
          }
          .count {
            background: $color-orange
          }

        }
      }
    }

    // form
    .reg-con {
      width: 500px;
      margin: 60px auto 160px;
    }
  }
  // 注册协议
  .register-agree {
    width: 100%;
    height: 100%;
    position: absolute;
    top: 0px;
    left: 0px;
    
    background: rgba(0, 0, 0, .2);
    .box {
      width: 750px;
      height: 440px;
      background: #fff;
      position: fixed;
      top: calc(50% - 220px);
      left: calc(50% - 375px);
      padding: 20px;
      box-sizing: border-box;
      color: #666;
      font-size: 13px;
      border-top: 2px solid $color-red;
      .title {
        color: #000;
        font-size: 18px;
        position: relative;
        margin-bottom: 16px;
        a {
          display: inline-block;
          position: absolute;
          right: 0px;
          top: 0px;
        }
      }

      .con {
        p {
          line-height: 1.4;
          margin-bottom: 10px;

          span.under {
            text-decoration: underline;
          }
          span.bold {
            font-weight: bold;
            color: #333;
          }
        }
        a {
          display: inline-block;
          margin-bottom: 8px;
          line-height: 1.6;
          color: $color-orange;
          &:hover {
            text-decoration: underline;
          }
        }
        
      }
      .btn {
        display: block;
        margin: 0 auto;
        padding: 0 20px;
        background: $color-orange;
        color: #fff;
        line-height: 32px;
        width: 140px;
        border-radius: 3px;
        transition: .4s;
        &:hover {
          background: $color-red;
        }
      }
    }
  }
}
</style>



