// 倒计时，时分秒
export function countDown({ hour, minute, second }) {
  hour = parseInt(hour)
  minute = parseInt(minute)
  second = parseInt(second)
  let total = hour * 60 * 60 + minute * 60 + second
  
  if (!total) return false

  total -= 1
  let newSecond = total % 60
  total -= newSecond
  let newMinute = total % (60 * 60) / 60
  total -= newMinute * 60
  let newhour = total / 60 / 60
  // 小于10的数字前面补0
  newSecond < 10 && (newSecond = '0' + newSecond)
  newMinute < 10 && (newMinute = '0' + newMinute)
  newhour < 10 && (newhour = '0' + newhour)
  return { hour: newhour, minute: newMinute, second: newSecond }
}



