<template>
  <div class="headIndexLogin">
    <!-- 顶部菜单 -->
    <div class="top-bar">
      <div class="wrap">
        <div class="top-left">
          <!-- 中国大陆 -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeWebPlace">
            <span>
              {{place}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-for="(item, index) in webPlace"
                :key="index" :command="item.value">
                {{item.place}}
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!-- 登录注册 -->
          <el-dropdown v-if='userName' class='top-dropdown' size='small' trigger="hover" @command="logoutFunc">
            <span>
              {{userName}}<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown" style='width: 100px'>
              <el-dropdown-item command="1">
                退出
              </el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!-- <router-link v-if='userName' class='bar-link red' to=''>{{userName}}<i class="el-icon-arrow-down el-icon--right"></i></router-link> -->
          <router-link v-if='!userName' class='bar-link red' to='/login'>亲，请登陆</router-link>
          <router-link v-if='!userName' class='bar-link' to='/register'>免费注册</router-link>
          <router-link class='bar-link' to='/login'>手机逛淘宝</router-link>
        </div>
        <div class="top-right">
          <!-- 我的淘宝 -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeMytaobao">
            <span>
              我的淘宝<i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1">已买到的宝贝</el-dropdown-item>
              <el-dropdown-item command="2">我的足迹</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!-- 购物车 -->
          <router-link class='bar-link' to=''><i class='fa fa-cart-plus'></i><span>购物车</span><i class='el-icon-arrow-down'></i></router-link>
          <!-- 收藏夹 -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeCollect">
            <span>
              <i class='fa fa-star'></i><span class='tab'>收藏夹</span><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1">收藏的宝贝</el-dropdown-item>
              <el-dropdown-item command="2">收藏的店铺</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!-- 商品分类 -->
          <router-link class='bar-link' to=''><span style='margin-right: 10px;'>商品分类</span>|</router-link>
          <!-- 卖家中心 -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeSellect">
            <span>
              <span class='tab'>卖家中心</span><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1">免费开店</el-dropdown-item>
              <el-dropdown-item command="2">已卖出的宝贝</el-dropdown-item>
              <el-dropdown-item command="3">出售中的宝贝</el-dropdown-item>
              <el-dropdown-item command="4">卖家服务市场</el-dropdown-item>
              <el-dropdown-item command="5">卖家培训中心</el-dropdown-item>
              <el-dropdown-item command="6">体验中心</el-dropdown-item>
              <el-dropdown-item command="7">问商友</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!-- 联系客服 -->
          <el-dropdown class='top-dropdown' size='small' trigger="hover" @command="changeCustom">
            <span>
              <span class='tab'>联系客服</span><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item command="1">消费者客服</el-dropdown-item>
              <el-dropdown-item command="2">卖家客服</el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
          <!-- 网站导航 -->
          <el-dropdown class='top-dropdown top-nav-bar' size='small' trigger="hover" @command="changeSellect">
            <span>
              <i class='fa fa-bars'></i><span class='tab'>网站导航</span><i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu style='width: 1190px; padding: 10px 0' slot="dropdown">
              <el-row class='nav-row'>
                <!-- 主题市场 -->
                <el-col class='nav-col' :span="6">
                  <h4 class='nav-title nav-title-1'>主题市场</h4>
                  <div class='nav-items'>
                    <el-dropdown-item class='nav-item' v-for='(item, index) in headNav.theme' :key='index' :command="item.value">{{item.name}}</el-dropdown-item>
                  </div>
                </el-col>
                <el-col class='nav-col' :span="6">
                  <h4 class='nav-title nav-title-2'>特色市场</h4>
                  <div class='nav-items'>
                    <el-dropdown-item class='nav-item' v-for='(item, index) in headNav.feature' :key='index' :command="item.value">{{item.name}}</el-dropdown-item>
                  </div>
                </el-col>
                <el-col class='nav-col' :span="6">
                  <h4 class='nav-title nav-title-3'>阿里APP</h4>
                  <div class='nav-items'>
                    <el-dropdown-item class='nav-item' v-for='(item, index) in headNav.aliapp' :key='index' :command="item.value">{{item.name}}</el-dropdown-item>
                  </div>
                </el-col>
                <el-col class='nav-col' :span="6">
                  <h4 class='nav-title nav-title-4'>精彩推荐集</h4>
                  <div class='nav-items'>
                    <el-dropdown-item class='nav-item' v-for='(item, index) in headNav.recommend' :key='index' :command="item.value">{{item.name}}</el-dropdown-item>
                  </div>
                </el-col>
              </el-row>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'headIndexLogin',
  computed: {
    ...mapGetters(['webPlace', 'headNav', 'userName'])
  },
  data () {
    return {
      place: '中国大陆',
      searchTab: 0,
      search: '',
      moreLinks: [
        { name: '新款连衣裙', id: 1 },
        { name: '四件套', id: 2 },
        { name: '潮流T恤', id: 3 },
        { name: '时尚女鞋', id: 4 },
        { name: '短裤', id: 5 },
        { name: '半身裙', id: 6 },
        { name: '男士外套', id: 7 },
        { name: '墙纸', id: 8 },
        { name: '行车记录仪', id: 9 }
      ],
      moreLink: 0
    }
  },
  methods: {
    // top
    // 修改地区版本
    changeWebPlace(command) {
      this.webPlace.filter(item => {
        if (item.value === command) {
          this.place = item.place
        }
      })
    },
    // 退出
    logoutFunc(command) {
      if (command === '1') {
        this.$message.success('退出成功')
        this.$cookie.set('userName', '')
        this.$router.push('/login')
      }
    },
    // 我的淘宝
    changeMytaobao(command) {
      alert(command)
    },
    // 收藏夹
    changeCollect(command) {
      alert(command)
    },
    // 卖家中心
    changeSellect(command) {
      alert(command)
    },
    // 联系客服
    changeCustom(command) {
      alert(command)
    }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../../assets/css/public.scss';
// 顶部导航
.headIndexLogin{
  background: #fff;
  .top-bar {
    width: 100%;
    height: 36px;
    background: #f5f5f5;
    border-bottom: 1px solid #eee;
    .wrap {
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      .bar-link {
        padding: 0 3px;
        &.red, &:hover {
          color: $color-red;
        }
      }
      .top-dropdown {
        height: 36px;
        line-height: 36px;
        padding: 0 3px;
        font-size: 12px;
        &:hover {
          cursor: pointer;
          span {
            color: $color-red
          }
        }
        span.tab {
          display: inline-block;
          margin-left: 3px;
          margin-right: 2px;
        }
      }
      .top-left {
        width: 400px;
        height: 36px;
      }
      .top-right {
        display: flex;
        justify-content: flex-end;
        width: 700px;
        height: 36px;
        line-height: 36px;
        .bar-link {
          margin-left: 5px;
          margin-right: 2px;
          span {
            display: inline-block;
            margin-left: 3px;
            margin-right: 5px;
          }
        }
      }

    }
  }
}
// 网站导航
.nav-row {
  display: flex;
  .nav-col {
    border-right: 1px solid #eee;
    padding-left: 7px;
    align-self: stretch;
  }
  .nav-title {
    padding-left: 10px;
  }
  .nav-title-1 {
    color: #fc4200;
  }
  .nav-title-2 {
    color: #9fb838;
  }
  .nav-title-3 {
    color: #d75696;
  }
  .nav-title-4 {
    color: #2e9fdb;
  }
  .nav-items {
    display: flex;
    justify-content: flex-start;
    flex-wrap: wrap;
    margin-top: 10px;
  }
  .nav-item {
    flex: 0 0 40px;
  }
}
</style>



