<template>
  <div class="goodGoods">
    <div class="wrap">
      <!-- 有好货 -->
      <div class="goodGoods-left goodGoods-item">
        <!-- 头部 -->
        <div class="public-head-bar">
          <img class='public-head-bar-img' src="../../assets/image/index/ggs-left-logo.png" alt="">
          <div class="public-qr-bar" @mouseenter='showqr = true' @mouseleave='showqr = false'>
            <i class="fa fa-qrcode" aria-hidden="true"></i>
            <transition name='qrAnim'>
              <div class="public-qr-ab" v-if='showqr'>
                <img src="../../assets/image/index/ggs-qr.png" alt="">
                <p>手机淘宝扫码</p>
              </div>
            </transition>
          </div>
          <span class='public-desc'>与品质生活不期而遇</span>
          <router-link class='public-more' to=''>换一换</router-link>
        </div>
        <!-- 列表 -->
        <div class="list">
          <router-link to='' v-for='(item, index) in ggsList' :key='index' class="list-item">
            <div class="item-img-box">
              <!-- item.imgUrl -->
              
              <img v-lazy="item.imgUrl" alt="">
            </div>
            
            <p class='left-1'>{{item.title}}</p>
            <p class='left-2'>{{item.desc}}</p>
            <p class='left-3'><i class="fa fa-smile-o" aria-hidden="true"></i> {{item.number}}人说好</p>
          </router-link>
        </div>
      </div>
      <!-- 爱逛街 -->
      <!-- ../../assets/image/index/ggs-right-logo.png -->
      <div class="goodGoods-right goodGoods-item">
        <!-- 头部 -->
        <div class="public-head-bar">
          <img class='public-head-bar-img' src="../../assets/image/index/ggs-right-logo.png" alt="">
          <span class='public-desc'>献给聪明可爱的你</span>
          <router-link class='public-more' to=''>更多<i class='el-icon-arrow-right'></i></router-link>
        </div>
        <!-- 列表 -->
        <div class="list">
          <router-link to='' v-for='(item, index) in agjList' :key='index' class="list-item">
            <div class="item-img-box">
              <img v-lazy="item.imgUrl" alt="">
            </div>
            
            <div class='right-1'>
              <i class="fa fa-quote-left" aria-hidden="true"></i>
              <span>{{item.title}}</span>
            </div>
            <div class='right-2'>
              <div class="img-bar"><img :src="item.avatar" alt=""/></div>
              <span>{{item.username}}</span>
            </div>
          </router-link>
        </div>
      </div>

    </div>
  </div>
</template>

<script>
import { getGgs, getAgj } from '@/api'
export default {
  name: 'goodGoods',
  data () {
    return {
      showqr: false,
      ggsList: [],
      agjList: []
    }
  },
  computed: {
    
  },
  created() {
    
  },
  mounted() {
    this.getGgs(); // 有好货
    this.getAgj(); // 爱逛街
  },
  methods: {
    getGgs() {
      getGgs().then(res => {
        this.ggsList = res.data.list
      })
    },
    getAgj() {
      getAgj().then(res => {
        this.agjList = res.data.list
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../../assets/css/public.scss';
.goodGoods {
  margin-top: 10px;
  width: 100%;
  height: 604px;
  .wrap {
    display: flex;
    justify-content: space-between;
    .goodGoods-item {
      width: 550px;
      background: #fff;
      padding: 20px 20px 0;
      // 列表
      .list {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        margin-top: 20px;
        .list-item {
          display: block;
          width: 180px;
          height: 254px;
          // border: 1px solid red;
          margin-bottom: 16px;
          .item-img-box {
            width: 180px;
            height: 180px;
            background: #4f4f4f;
            img {
              width: 180px;
              height: 180px;
              opacity: .9;
              transition: .5s;
            }
          }
          // left
          .left-1 {
            font-size: 14px;
            color: #333;
            margin-top: 10px;
          }
          .left-2 {
            width: 180px;
            font-size: 12px;
            color: #666;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            margin-top: 10px
          }
          .left-3 {
            font-size: 12px;
            color: #35b1ff;
            margin-top: 16px;
          }
          // right
          .right-1 {
            color: #666;
            font-size: 14px;
            width: 170px;
            height: 34px;
            overflow: hidden;
            margin-top: 8px;
            padding: 0 5px;
            i {
              position: relative;
              color: #ddd;
              font-size: 20px;
              top: -3px;
              left: 3px;
              margin-right: 6px;
            }
          }
          .right-2 {
            margin-top: 10px;
            color: #666;
            height: 22px;
            .img-bar {
              display: inline-block;
              width: 22px;
              height: 22px;
              border-radius: 11px;
              vertical-align: middle;
              overflow: hidden;
              margin-left: 5px;
              
              img {
                width: 22px;
                height: 22px;
              }
            }
            span {
              display: inline-block;
              line-height: 22px;
              height: 22px;
              margin-left: 10px;
            }
          }

          &:hover {
            .item-img-box img {
              opacity: .6;
            }
            .left-1, .right-1 {
              color: $color-red
            }
          }
        }
      }
    }
    // 有好货
    .goodGoods-left {

    }
  }
}
// 动画
// 二维码
.qrAnim-enter-active {
  animation: qrAnim-in .3s;
}
.qrAnim-leave-active {
  animation: qrAnim-in .3s reverse;
}
@keyframes qrAnim-in {
  0% {
    top: 20px;
    opacity: 0;
  }
  100% {
    top: 24px;
    opacity: 1;
  }
}
</style>



