<template>
  <div class="mrhdtbzb">
    <div class="wrap">
      <!-- 每日好店 -->
      <div class="mrhdtbzb-item">
        <div class="public-head-bar">
          <img class='public-head-bar-img' src="../../assets/image/index/mrhd-logo.png" alt="">
          <span class='public-desc'>发现深藏的好店</span>
          <router-link class='public-more' to=''>更多<i class='el-icon-arrow-right'></i></router-link>
        </div>
        <div class="items">
          <div class="item" v-for='(item, index) in mrhdList' :key='index'>
            <div class="item-head">
              <p>{{item.title}}</p><span>{{item.desc}}</span>
            </div>
            <div class="item-l item-box">
              <img v-lazy="item.imgUrl[0]" alt="">
            </div>
            <div class="item-r">
              <div class="item-box">
                <img v-lazy="item.imgUrl[1]" alt="">
              </div>
              <div class="item-box item-r-b">
                <img v-lazy="item.imgUrl[2]" alt="">
              </div>
            </div>
          </div>
        </div>
      </div>

      <!-- 淘宝直播 -->
      <div class="mrhdtbzb-item">
        <div class="public-head-bar">
          <img class='public-head-bar-img' src="../../assets/image/index/tbzb-logo.png" alt="">
          <span class='public-desc'>你的爱豆直播等你呦</span>
          <!-- <router-link class='public-more' to=''>更多<i class='el-icon-arrow-right'></i></router-link> -->
        </div>
        <div class="items">
          <div class="item" v-for='(item, index) in tbzbList' :key='index'>
            <div class="item-head">
              <p>{{item.title}}</p><span>{{item.desc}}</span>
            </div>
            <div class="item-l item-box">
              <img v-lazy="item.imgUrl[0]" alt="">
            </div>
            <div class="item-r">
              <div class="item-box">
                <img v-lazy="item.imgUrl[1]" alt="">
              </div>
              <div class="item-box item-r-b">
                <img v-lazy="item.imgUrl[2]" alt="">
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { getMrhd, getTbzb } from '@/api'
export default {
  name: 'userHome',
  data () {
    return {
      mrhdList: [],
      tbzbList: []
    }
  },
  computed: {
    
  },
  created() {
    
  },
  mounted() {
    this.getMrhd()
    this.getTbzb()
  },
  methods: {
    // 每日好店
    getMrhd() {
      getMrhd().then(res => {
        this.mrhdList = res.data.list
      })
    },
    // 淘宝直播
    getTbzb() {
      getTbzb().then(res => {
        console.log(res)
        this.tbzbList = res.data.list
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../../assets/css/public.scss';
.mrhdtbzb {
  width: 100%;
  margin-top: 10px;
  height: 514px;
  .wrap {
    box-sizing: border-box;
    display: flex;
    justify-content: space-between;
    .mrhdtbzb-item {
      width: 550px;
      padding: 20px;
      background: #fff;
      .items {
        display: flex;
        justify-content: space-between;
        flex-wrap: wrap;
        .item {
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          width: 271px;
          .item-head {
            width: 100%;
            margin: 14px 0 10px;
            p {
              display: inline-block;
              color: #333;
              line-height: 20px;
              font-size: 16px;
            }
            span {
              display: inline-block;
              line-height: 20px;
              margin-left: 6px;
              color: #666;
              font-size: 14px;
            }
          }
          .item-box {
            background: $color-img;
            cursor: pointer;
            img {
              display: block;
              transition: .3s;
              opacity: .9;
            }
            &:hover {
              img {
                opacity: .7;
              }
            }
          }
          .item-l {
            width: 180px;
            height: 180px;
            img {
              width:180px;
              height: 180px;
            }
          }
          .item-r {
            .item-box {
              width: 89px;
              height: 89px;
              img {
                display: block;
                width: 89px;
                height: 89px;
              }
            }
            .item-r-b {
              margin-top: 2px;
              img {
                display: block;
                width: 89px;
                height: 89px;
              }
            }
            
          }
          &:hover {
            .item-head p {
              color: $color-red;
            }
          }
        }
      }
    }
  }
}
</style>



