<template>
  <div :class="winScroll>100?'menuRight fixed': 'menuRight'">
  <!-- <div class="menuRight fixed"> -->
    <div class="menu-bar-img"></div>
    <div class="menu-bar-items">
      <ul>
        <li :class='winScroll<offsetTopArr[1]? "active": ""' @click='scrollPoint(0)'>
          <span>
            爱逛<br/>好货
          </span>
        </li>
        <li :class='(winScroll>=offsetTopArr[1] && winScroll<offsetTopArr[2] )? "active": ""'  @click='scrollPoint(1)'>
          <span>
            好店<br/>直播
          </span>
        </li>
        <li :class='(winScroll>=offsetTopArr[2] && winScroll<offsetTopArr[3] )? "active": ""'  @click='scrollPoint(2)'>
          <span>
            品质<br/>特色
          </span>
        </li>
        <li :class='(winScroll>=offsetTopArr[3] && winScroll<offsetTopArr[4] )? "active": ""'  @click='scrollPoint(3)'>
          <span>
            实惠<br/>热卖
          </span>
        </li>
        <li :class='winScroll>=offsetTopArr[4]? "active": ""'  @click='scrollPoint(4)'>
          <span>
            猜你<br/>喜欢
          </span>
        </li>
        <li v-show='winScroll>200' @click='toTop'>
          <span>
            <i class='el-icon-arrow-up'></i><br/>顶部
          </span>
        </li>
        <li>
          <span>
            反馈
          </span>
        </li>
        <li>
          <span>
            暴恐<br/>举报
          </span>
        </li>
      </ul> 
    </div>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
export default {
  name: 'menuRight',
  data () {
    return {
      showTotop: false,
      offsetTopArr: [950, 1908, 2740, 3540, 4735],
      winScroll: 0
    }
  },
  computed: {
  },
  created() {
  },
  mounted() {
    // let winScroll = 0;
    $(document).scroll(() => {
      this.winScroll = $(document).scrollTop()
      this.SET_WINSCROLL(this.winScroll)
    })

  },
  methods: {
    ...mapMutations(['SET_WINSCROLL']),
    
    scrollFunc(num) {
      $('body,html').stop().animate({'scrollTop': num}, 400)
    },
    toTop() {
      this.scrollFunc(0);
    },
    // 点击事件
    scrollPoint(index) {
      if (!index) {
        this.scrollFunc(0);
      } else {
        this.scrollFunc(this.offsetTopArr[index] + 3)
      }
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../assets/css/public.scss';
.menuRight {
  width: 50px;
  height: 300px;
  position: absolute;
  top: 197px;
  left: 50%;
  margin-left: 606px;
  .menu-bar-img {
    background: url('../assets/image/common/menu.png') top center no-repeat;
    padding-top: 22px;
  }
  .menu-bar-items li {
    height: 50px;
    text-align: center;
    font-size: 14px;
    background: #fff;
    transition: background-color,color .2s;
    cursor: pointer;
    span {
      display: inline-block;
      width: 34px;
      margin: 7px auto 0;
      line-height: 18px;
      height: 42px;
      border-bottom: 1px solid $background;
      font-size: 13px;
    }
    &:first-child {
      border-radius: 5px 5px 0 0;
      // background: $color-orange;
      // color: #fff;
      span {
        color: $color-red;
        // border: none
      }
    }
    &:nth-child(2) {
      color: #f05;
    }
    &:nth-child(3) {
      color: #A8C001;
    }
    &:nth-child(4) {
      color: #8d7afb;
    }
    &:nth-child(5) {
      color: #F40;
    }
    &:nth-child(6) {
      span {
        margin-top: 5px;
        height: 44px;
      }
    }
    &:nth-child(7) {
      span {
        margin-top: 15px;
        height: 34px;
      }
    }
    &:last-child {
      border-radius: 0 0 5px 5px;
      overflow: hidden;
      span {
        border-bottom: none;
        
      }
    }
    &:hover,&.active {
      background: $color-orange;
      color: #fff;
      span {
        color: #fff;
        border-color: $color-orange;
      }
    }
  }
  &.fixed {
    position: fixed;
    top: 100px;
  }
}
</style>



