import Mock from 'mockjs'

import classifyAPI from './index/bannerClass'
import ggsAPI from './index/getGgs'
import getAgjAPI from './index/getAgj'
import getRushBuyAPI from './index/getRushBuy'
import getMrhdAPI from './index/getMrhd'
import getTbzbAPI from './index/getTbzb'

Mock.setup({
  timeout: '350-600'
})

// mock 数据，地址为异步请求 api 中的url
// 首页
Mock.mock(/\/getClassify/, 'post', classifyAPI.getClassify)
Mock.mock(/\/getGgs/, 'get', ggsAPI.getGgs) // 有好货
Mock.mock(/\/getAgj/, 'get', getAgjAPI.getAgj) // 爱逛街
Mock.mock(/\/getRushBuy/, 'get', getRushBuyAPI.getRushBuy) // 海淘抢
Mock.mock(/\/getMrhd/, 'get', getMrhdAPI.getMrhd) // 每日好店
Mock.mock(/\/getTbzb/, 'get', getTbzbAPI.getTbzb) // 淘宝直播

export default Mock



