<template>
  <div class="rushBuy">
    <div class="wrap">
      <div class="logo">
        <div class="left">
          <img src="../../assets/image/index/rushbuy-logo.png" alt="" />
          <div class='count'>距结束<p><span>{{rushbuyCount.hour}}</span>:<span>{{rushbuyCount.minute}}</span>:<span>{{rushbuyCount.second}}</span></p></div>
        </div>
        <div class="right">
          <router-link to=''>换一换</router-link>
          <router-link to=''>更多<i class='el-icon-arrow-right'></i></router-link>
        </div>
      </div>
      <div class="items">
        <div class="item" v-for='(item, index) in rushBuyList' :key='index'>
          <div class="item-img">
            <img v-lazy="item.imgUrl" alt="">
          </div>
          <div class="item-r">
            <div class="item-title">{{item.title}}</div>
            <span class='item-desc'>{{item.desc}}</span>
            <el-progress class='item-progress' type="line" :percentage="item.percentage" status='exception'></el-progress>
            <div class='item-progress-desc'>
              <span>{{item.percentage}}%</span>
              <span>已抢{{item.piect}}件</span>
            </div>
            <div class="item-money">
              ￥
              <span class='item-money-now'>{{item.price}}</span>
              <span class='item-money-old'><s>{{item.priceOld}}</s></span>
            </div>
          </div>
        </div>
      </div>
    </div>
  </div>
</template>

<script>
import { countDown } from '../../utils'
import { getRushBuy } from '@/api'
export default {
  name: 'rushBuy',
  data () {
    return {
      rushbuyCount: {
        hour: '00', 
        minute: '00', 
        second: '00'
      },
      rushBuyList: []
    }
  },
  computed: {
  },
  created() {
    this.getRushBuy();
  },
  mounted() {
  },
  methods: {
    // 倒计时
    myCountDown() {
      let countInter = setInterval(() => {
        if (!countDown(this.rushbuyCount)) {
          clearInterval(countInter)
          return false
        }
        this.rushbuyCount = countDown(this.rushbuyCount)
        // console.log(JSON.stringify(this.rushbuyCount));
      }, 1000)
    },
    // 获取海淘抢数据
    getRushBuy() {
      getRushBuy().then(res => {
        // console.log(res)
        this.rushbuyCount = res.data.count
        // console.log(res.count)
        this.rushBuyList = res.data.list
        this.myCountDown()
      })
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../../assets/css/public.scss';
.rushBuy {
  width: 100%;
  margin-top: 10px;
  height: 262px;
  .wrap {
    background: #fff;
    padding: 20px;
    box-sizing: border-box;
    .logo {
      display: flex;
      justify-content: space-between;
      .left {
        display: flex;
        justify-content: flex-start;
        
        .count {
          margin-left: 10px;
          line-height: 22px;
          p {
            display: inline-block;
            color: $color-red;
            margin-left: 3px;
            span {
              display: inline-block;
              padding: 0 3px;
              margin: 0 2px;
              border-radius: 2px;
              background: $color-pink;
            }
          }
        }
        img {
          height: 22px;
        }
      }
      .right {
        a {
          line-height: 22px;
          margin-left: 20px;
          &:hover {
            color: $color-red
          }
        }
      }
    }
    .items {
      display: flex;
      justify-content: space-between;
      margin-top: 20px;
      .item {
        flex: 0 0 33%;
        display: flex;
        justify-content: flex-start;
        cursor: pointer;
        .item-img {
          width: 180px;
          height: 180px;
          background: $color-img;
          img {
            transition: .5s;
            width: 180px;
            height: 180px;
            opacity: .9;
          }
        }
        .item-r {
          margin-left: 10px;
          width: 181px;
          height: 180px;
          overflow: hidden;
          .item-title {
            font-size: 16px;
            line-height: 1.3;
            height: 40px;
            overflow: hidden;
            margin-top: 3px;
          }
          .item-desc {
            display: block;
            margin-top: 36px;
          }
          .item-progress {
            width: 230px;
            margin-top: 4px;
          }
          .item-progress-desc {
            display: flex;
            justify-content: space-between;
            color: $color-red;
            margin-top: 4px;
          }
          .item-money {
            font-size: 12px;
            color: #666;
            margin-top: 10px;
            .item-money-now {
              font-size: 18px;
              color: $color-red
            }
            .item-money-old {
              display: inline-block;
              padding-left: 4px;
            }
          }
        }
        &:hover {
          .item-img img {
            opacity: .6;
          }
          .item-r .item-title {
            color: $color-red;
          }
        }
      }
    }
    
  }

}
</style>



