import axios from 'axios'
import router from '@/router'
// elementui
import ElementUI from 'element-ui'
let $msgbox = ElementUI.MessageBox
let $alert = ElementUI.MessageBox.alert
let $confirm = ElementUI.MessageBox.confirm
let $prompt = ElementUI.MessageBox.prompt
let $message = ElementUI.Message

// 创建axios实例
const service = axios.create({
  baseURL: '/api', // 请求地址
  timeout: 5000
})

// request 拦截器
service.interceptors.request.use(config => {
  // Do something before request is sent
  console.log('发送数据', config.data)
  return config
}, error => {
  // console.log(error)
  Promise.reject(error)
})

// response 拦截器
service.interceptors.response.use(
  // response => response
  response => {
    console.log('返回数据', response.data)
    let res = response.data
    let code = res.code
    if (code === 0) {
      return response.data
    } else {
      // $message({
      //   type: 'error',
      //   msg: res.data.msg
      // })
      return Promise.reject(res.data.msg)
    }
    
  },
  error => {
    // console.log('err' + error) // for debug
    $message({
      message: '错误：' + error.message,
      type: 'error',
      duration: 5 * 1000
    })
    return Promise.reject(error)
  }
)

export default service



