import { asyncRouterMap, constantRouterMap } from '@/router'

// 通过meta.role判断是否与当前用户权限匹配
let hasPermission = (router, userRole) => {
  if (router.meta && router.meta.role) {
    let thisRole = router.meta.role
    return thisRole.some(thisRole => router.meta.role.indexOf(userRole) >= 0)
  } else {
    return true
  }
}

// 递归过滤异步路由表，反返回符合用户角色权限的路由表
let filterAsyncRouter = (asyncRouterMap, userRole) => {
  const accessedRouters = asyncRouterMap.filter(router => {
    if (hasPermission(router, userRole)) {
      if (router.children && router.children.length) {
        router.children = filterAsyncRouter(router.children, userRole)
      }
      return true
    }
    return false
  })
  return accessedRouters
}

const permission = {
  state: {
    routers: constantRouterMap,
    addRouters: []
  },
  mutations: {
    SET_ROUTERS: (state, routers) => {
      state.addRouters = routers
      state.routers = constantRouterMap.concat(routers)
    }
  },
  actions: {
    GenerateRoutes({ commit }, userRole) {
      return new Promise((resolve, reject) => {
        let accessedRouters = filterAsyncRouter(asyncRouterMap, userRole)
        commit('SET_ROUTERS', accessedRouters)
        resolve()
      })
    }
  }
}

export default permission



