<template>
  <div class="swiper">
    <div :class="'swiper-container ' + el" :style='{width: width, height: height}'>
      <div class="swiper-wrapper">
        <div class="swiper-slide" v-for='(item, index) of swiperArr' :key='index'>
          <router-link to=''><img :src="item" alt="" /></router-link>
        </div>
        
      </div>
      <!-- 如果需要分页器 -->
      <div v-if='showPagination' class="swiper-pagination"></div>
      
      <!-- 如果需要导航按钮 -->
      <div v-if='showButton' class="swiper-button-prev"><i class='el-icon-arrow-left'></i></div>
      <div v-if='showButton' class="swiper-button-next"><i class='el-icon-arrow-right'></i></div>
      
      <!-- 如果需要滚动条 -->
      <!-- <div class="swiper-scrollbar"></div> -->
    </div>
  </div>
</template>

<script>
// 使用
// <swiper width='520px' height='280px' el='swiper1' :swiperArr='swiper1Arr' showPagination='true' showButton='true' />
// 图片引入方式为require('url'),url为图片相对于引入组件的.vue文件的相对路径
export default {
  name: 'swiper',
  props: ['direction', 'el', 'width', 'height', 'swiperArr', 'showPagination', 'showButton'],
  data () {
    return {
    }
  },
  mounted() {
    // console.log(this.swiperArr)
    // my-swiper
    let vthis = this;

    // var myPlugin = {
    //   name: 'debugger',
    //   params: {
    //     debugger: false
    //   },
    //   on: {
    //     slideChange: function() {
    //       // alert(2222)
    //       if (!this.params.debugger) return;
    //       console.log(this.activeIndex)
    //       if (this.activeIndex > 6) {
    //         vthis.swiper2Count = 1
    //       } else if (this.activeIndex < 1) {
    //         vthis.swiper2Count = 6
    //       } else {
    //         vthis.swiper2Count = this.activeIndex
    //       }
    //     }
    //   }
    // };
    // Swiper.use(myPlugin);

    var mySwiper = new Swiper('.' + this.el, {
      // 滚动方向，默认横向 horizontal ,纵向 vertical
      direction: this.direction,
      // 是否循环滚动
      loop: true,
      // 是否自动滚动
      autoplay: {
        delay: 5000,
        // stopOnLastSlide: false, // 当切换到最后一个slide 时，是否停止自动切换
        disableOnInteraction: false // 触碰后是否停止自动切换
      },
      // 分页
      pagination: {
        el: '.swiper-pagination',
        clickable: true
      },
      // 前进后退按钮
      navigation: {
        nextEl: '.swiper-button-next',
        prevEl: '.swiper-button-prev'
      }
      // debugger: true
    });
    mySwiper.el.onmouseenter = function() {
      mySwiper.autoplay.stop()
    }
    mySwiper.el.onmouseleave = function() {
      mySwiper.autoplay.start()
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../assets/css/public.scss';
</style>



