import router from './router'
import store from './store'
// import NProgress from 'nprogress' // progress 进度条
// import 'ngrogress/ngrogress.css' // progress 样式

router.beforeEach((to, from, next) => {
  let userRole = store.getters.userRole
  // 判断是否登录
  if (!userRole) {
    // 判断是否进入会员中心
    if (to.path === '/user') {
      alert('您尚未登录，请登陆后再访问会员中心')
      next('/login')
    } else {
      next()
    }
  } else {
    // 当已经登录时，动态添加路由
    // 生成可访问的路由表
    store.dispatch('GenerateRoutes', userRole).then(() => {
      // 动态添加可访问路由表
      router.addRoutes(store.getters.addRouters)
      next({ ...to }) // hack方法，确保addRoutes已经完成
    })
    next()
  }
})



