<template>
  <div class="headSearch">
    <!-- 中间搜索 -->
    <div class="center-bar wrap">
      <router-link to='/' class="logo"><img src="../../assets/logo.png" alt=""></router-link>
      <!-- search -->
      <div class="center-search">
        <ul class='search-tab'>
          <li :class='searchTab === 0?"active":""'><span @click='changeTab(0)'>宝贝</span></li>
          <li :class='searchTab === 1?"active":""'><span @click='changeTab(1)'>天猫</span></li>
          <li :class='searchTab === 2?"active":""'><span @click='changeTab(2)'>店铺</span></li>
        </ul>

        <div class="search-input-box">
          <i @click='upfile' class='icon-camera fa fa-camera'></i>
          <i class='icon-search fa fa-search'></i>
          <input v-model='search' type="text">
          <button @click='searchFunc'>搜索</button>
          <input id='upfile' type="file">
        </div>
        <div class='more-box'>
          <div class='link-box'>
            <a href='javascript:void(0)' @click='clickLink(item.id)' v-for='item in moreLinks' :key='item.id' :class='item.id === moreLink ?"more active":"more"'>{{item.name}}</a>
          </div>
          <a href='javascript:void(0)' class='more' @click='clickMore'>更多 ></a>
        </div>
      
      </div>
      <!-- 二维码 -->
      <div class="center-qr">
        <img src="../../assets/image/qrcode.png" alt="">
      </div>
    </div>
    <!-- 底部导航 -->
    <div class="bottom">
      <div class="wrap">
        <span class='first'>主题市场</span>
        <router-link to=''>天猫<img src="" alt=""></router-link>
        <router-link to=''>聚划算</router-link>
        <router-link to=''>天猫超市</router-link>
        <span>|</span>
        <router-link to=''>淘抢购</router-link>
        <router-link to=''>电器城</router-link>
        <router-link to=''>司法拍卖</router-link>
        <router-link to=''>中国质造</router-link>
        <router-link to=''>兴农扶贫</router-link>
        <span>|</span>
        <router-link to=''>飞猪旅行</router-link>
        <router-link to=''>智能生活</router-link>
        <router-link to=''>苏宁易购</router-link>
      </div>
      
    </div>
  </div>
</template>

<script>
import { mapGetters, mapActions } from 'vuex'
export default {
  name: 'headSearch',
  computed: {
    ...mapGetters(['webPlace', 'headNav'])
  },
  data () {
    return {
      place: '中国大陆',
      searchTab: 0,
      search: '',
      moreLinks: [
        { name: '新款连衣裙', id: 1 },
        { name: '四件套', id: 2 },
        { name: '潮流T恤', id: 3 },
        { name: '时尚女鞋', id: 4 },
        { name: '短裤', id: 5 },
        { name: '半身裙', id: 6 },
        { name: '男士外套', id: 7 },
        { name: '墙纸', id: 8 },
        { name: '行车记录仪', id: 9 }
      ],
      moreLink: 0
    }
  },
  methods: {
    // top
    // 修改地区版本
    changeWebPlace(command) {
      this.webPlace.filter(item => {
        if (item.value === command) {
          this.place = item.place
        }
      })
    },
    // 我的淘宝
    changeMytaobao(command) {
      alert(command)
    },
    // 收藏夹
    changeCollect(command) {
      alert(command)
    },
    // 卖家中心
    changeSellect(command) {
      alert(command)
    },
    // 联系客服
    changeCustom(command) {
      alert(command)
    },
    // center
    changeTab(val) {
      this.searchTab = val
      // console.log(tab, event)
    },
    upfile() {
      document.getElementById('upfile').click();
    },
    searchFunc() {
      alert(this.search)
    },
    // 搜索框下边的链接
    clickLink (id) {
      alert(id)
      this.moreLink = id;
    },
    clickMore() {
      alert('toLists')
    }
  }
  
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../../assets/css/public.scss';
// 顶部导航
.headSearch{
  background: #fff;
}

// 中间搜索部分
.center-bar {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 120px;
  .logo {
  }
  .center-qr img {
    width: 76px;
    height: 76px;
  }
  // 搜索框
  .search-tab {
    display: flex;
    margin-left: 16px;
    li {
      padding: 0 8px;
      height: 24px;
      line-height: 24px;
      color: $color-red;
      cursor: pointer;
      &:hover {
        background: #FFEEE5;
      }
      &.active {
        color: #fff;
        background: $color-orange;
        border-radius: 5px 5px 0 0;
      }
    }
  }
  .search-input-box {
    width: 650px;
    position: relative;
    input {
      float: left;
      width: 538px;
      line-height: 32px;
      padding-left: 30px;
      height: 32px;
      border-radius: 18px 0 0 18px;
      border: 2px solid $color-orange;
      border-right: 0;
    }
    button {
      display: inline-block;
      background-color: $color-orange;
      color: #fff;
      // font-size: 16px;
      text-align: left;
      padding-left: 20px;
      height: 36px;
      width: 74px;
      border-radius: 0 18px 18px 0;
      span {
        font-size: 16px;
      }
    }
    i {
      position: absolute;
      top: 10px;
      color: #999;
      font-size: 16px;
      &.icon-search {
        left: 10px;
      }
      &.icon-camera {
        cursor: pointer;
        right: 86px;
      }
    }
  }
  .more-box {
    display: flex;
    justify-content: space-between;
    margin-top: 4px;
    a:hover, a.active {
      color: $color-red
    }
    .link-box {
      a {
        display: inline-block;
        margin-right: 10px;
      }
    }
    .more {
      margin-right: 10px;
    }
  }
}
// 底部导航
.bottom {
  width: 100%;
  background: $color-orange;
  height: 30px;
    line-height: 30px;
  .wrap {
    display: flex;
    justify-content: flex-start;
    a, span {
      display: inline-block;
      position: relative;
      line-height: 30px;
      padding: 0px 12px;
      color: #fff;
      font-size: 14px;
      text-align: center;
    }
    a:hover:before {
      content: url(../../assets/image/head-nav-hover.jpg);
      font-family: iconfont;
      color: #ff5000;
      top: -19px;
      position: absolute;
      left: 50%;
      font-size: 12px;
      margin-left: -15px;
    }
    .first {
      width: 166px;
      background: #ff5000;
    }
  }
}
#upfile {
  display: none
}
</style>



