import fetch from '@/utils/fetch'
import md5 from 'js-md5'

export default ({ userName, password }) => {
  // alert(username)
  let data = {
    userName,
    password: md5(password)
  }
  // console.log(obj)
  return fetch({
    url: '/register',
    method: 'post',
    data
  })
}



