<template>
  <div id="app">
    <!-- <img src="./assets/logo.png"> -->
    <router-view/>
  </div>
</template>

<script>
import { mapMutations } from 'vuex'
export default {
  name: 'App',
  mounted() {
    let userName = this.$cookie.get('userName')
    if (userName) {
      this.SET_USERNAME(userName)
    }
  },
  methods: {
    ...mapMutations(['SET_USERNAME'])
  }
}
</script>

<style lang='scss'>
