const user = {
  state: {
    userName: '',
    userRole: 2,
    avator: '头像'
  },
  mutations: {
    SET_USERNAME: (state, userName) => {
      state.userName = userName
    },
    SET_USERROLE: (state, userRole) => {
      state.userRole = userRole
    },
    SET_AVATOR: (state, avator) => {
      state.avator = avator
    }
  },
  actions: {
    userNameChange({ commit }, userName) { 
      // 异步请求
      return new Promise((resolve, reject) => {
        setTimeout(() => {
          commit('SET_USERNAME', userName)
          console.log('user')
          resolve()
        }, 1000)
      })
    }
  }
}

export default user



