export default {
  state: {
    regState: 0,
    dragState: false,
    regPhone: ''
  },
  mutations: {
    SET_REGSTATE: (state, regState) => {
      state.regState = regState
    },
    SET_DRAGSTATE: (state, dragState) => {
      state.dragState = dragState
    },
    SET_REGPHONE: (state, val) => {
      state.regPhone = val
    }
  },
  actions: {
    
  }
}



