<script>
import { mapGetters, mapActions } from 'vuex'
import { getClassify } from '@/api/index'
import swiper from '@/components/swiper'

export default {
  name: 'banner',
  components: {
    swiper
  },
  data () {
    return {
      classifyList: [],
      navRightArr: [],
      showRightNav: false,
      swiper2Count: 1,
      bar3Index: 0,
      tab2Index: 0,
      // swiper
      swiper1Arr: [],
      // 阿里App
      appQr: [
        { name: '扫一扫淘宝', url: require('../../assets/image/qrcode.png') },
        { name: '扫一扫天猫', url: require('../../assets/image/qrcode.png') },
        { name: '扫一扫聚划算', url: require('../../assets/image/qrcode.png') },
        { name: '扫一扫支付宝', url: require('../../assets/image/qrcode.png') },
        { name: '扫一扫旺旺', url: require('../../assets/image/qrcode.png') },

        { name: '扫一扫淘票票', url: require('../../assets/image/qrcode.png') },
        { name: '扫一扫闲鱼', url: require('../../assets/image/qrcode.png') },
        { name: '扫一扫优酷', url: require('../../assets/image/qrcode.png') },
        { name: '扫一扫土豆', url: require('../../assets/image/qrcode.png') },
        { name: '扫一扫飞猪旅行', url: require('../../assets/image/qrcode.png') }
      ]
      
    }
  },
  computed: {
    ...mapGetters(['userName'])
  },
  created() {
    getClassify().then(res => {
      this.classifyList = res.data.list
    })

    // 组件化1
    this.swiper1Arr = [
      require('../../assets/image/index/swiper1-1.jpg'),
      require('../../assets/image/index/swiper1-2.jpg'),
      require('../../assets/image/index/swiper1-1.jpg'),
      require('../../assets/image/index/swiper1-2.jpg')
    ] 
  },
  mounted() {
    let vthis = this;
    // swiper2
    var myPlugin = {
      name: 'debugger',
      params: {
        debugger: false
      },
      on: {
        slideChange: function() {
          if (!this.params.debugger) return;
          // console.log(this.activeIndex)
          if (this.activeIndex > 6) {
            vthis.swiper2Count = 1
          } else if (this.activeIndex < 1) {
            vthis.swiper2Count = 6
          } else {
            vthis.swiper2Count = this.activeIndex
          }
        }
      }
    };
    Swiper.use(myPlugin);
    var mySwiper2 = new Swiper('.swiper2', {
      // 滚动方向，默认横向
      // direction: 'vertical',
      // 是否循环滚动
      loop: true,
      // 是否自动滚动
      autoplay: {
        delay: 5000,
        // stopOnLastSlide: false, // 当切换到最后一个slide 时，是否停止自动切换
        disableOnInteraction: false // 触碰后是否停止自动切换
      },
      // 分页
      // pagination: {
      //   el: '.swiper2-pagination',
      //   clickable: true
      // },
      // 前进后退按钮
      navigation: {
        nextEl: '.swiper2-next',
        prevEl: '.swiper2-prev'
      },
      debugger: true
    });
    mySwiper2.el.onmouseenter = function() {
      mySwiper2.autoplay.stop()
    }
    mySwiper2.el.onmouseleave = function() {
      mySwiper2.autoplay.start()
    }

    // 淘宝头条 
    var swiperTbtt = new Swiper('.swiper-tbtt', {
      // 滚动方向，默认横向 horizontal ,纵向 vertical
      direction: 'vertical',
      // 是否循环滚动
      loop: true,
      // 是否自动滚动
      autoplay: {
        delay: 5000,
        // stopOnLastSlide: false, // 当切换到最后一个slide 时，是否停止自动切换
        disableOnInteraction: false // 触碰后是否停止自动切换
      }
    });
    swiperTbtt.el.onmouseenter = function() {
      swiperTbtt.autoplay.stop()
    }
    swiperTbtt.el.onmouseleave = function() {
      swiperTbtt.autoplay.start()
    }

  },
  methods: {
    ...mapActions(['userNameChange']),
    // 左侧导航
    hoverNav(item) {
      this.showRightNav = true;
      let index = (item - 1) * 3
      this.navRightArr.length = 0;
      this.navRightArr.push(this.classifyList[index], this.classifyList[index + 1], this.classifyList[index + 2])
    },
    outNav() {
      this.showRightNav = false;
    },
    // 改变tab
    bar3Func(val) {
      this.bar3Index = val
    },
    // tab2 
    hoverTab2(index) {
      // console.log(index)
      this.tab2Index = index;
    },
    hideTab2() {
      this.tab2Index = 0;
    }
  }
}
</script>

