              a {
                display: inline-block;
                padding: 6px 10px 6px 0;
                
              }
            }
          }
        }
        // 猜你细化
        .item-right {
          padding-top: 20px;
          font-size: 18px;
          width: 240px;
          margin-left: 9px;
          .image-bar {
            .image-items {
              display: flex;
              justify-content: space-around;
              flex-wrap: wrap;
              .image-item {
                cursor: pointer;
                margin-top: 24px;
                img {
                  width: 100px;
                  height: 100px;
                }
                p {
                  text-align: center;
                  font-size: 12px;
                  line-height: 18px;
                }
                &:hover {
                  p {
                    color: $color-red;
                  }
                }
              }
            }
          }
        }
      }

    }
    // 中间部分
    .banner-main {
      margin-top: 10px;
      width: 520px;
      height: 510px;
      // 轮播图1
      .banner-main-swiper1 {
        width: 520px;
        height: 280px;
        background: #fff;
        .swiper1 {
          height: 280px;
        }
      }
      // 中间bar
      .swiper-bar {
        width: 520px;
        margin-top: 5px;
        .swiper-bar-top {
          display: flex;
          justify-content: space-between;
          height: 22px;
          .swiper-bar-top-1 {
            align-content: center;
            height: 22px;
            img {
              vertical-align: top;
              display: inline-block;
              margin-right: 20px;
            }
            span {
              display: inline-block;
              line-height: 22px;
              vertical-align: top;
            }
          }
          .swiper-bar-top-2 {
            line-height: 22px;
            height: 22px;
            span {
              color: $color-red;
            }
          }
        }
        .swiper-bar-border {
          display: flex;
          justify-content: space-between;
          .swiper-bar-border-item {
            width: 130px;
            height: 3px;
            background: #ff1648;
            &.active {
              background: #000;
            }
          }
        }
      }
      // 轮播图2
      .banner-main-swiper2 {
        background: #fff;
        width: 520px;
        height: 200px;
        .swiper2 {
          height: 200px;
          .swiper2-wrapper {
            .swiper-slide {
              display: flex;
              justify-content: space-between;
              flex-wrap: wrap;
              width: 520px;
            }
            // swiper2-1
            .swiper2-1 {
              a {
                display: inline-block;
              }
              img {
                width: 172px;
                height: 100px;
                border-right: $swiper2-border;
              }
              div {
                width: 173px;
                height: 201px;
                img {
                  height: 100px;
                  &:first-child {
                    border-bottom: $swiper2-border;
                  }
                }
              }
              .swiper2-1-1 {
                height: 201px;
                // border-right: 1px solid #ebebeb;
              }
              
            }
            // swiper2-2
            .swiper2-2 {
              justify-content: space-around;
              align-items: center;
              img {
                width: 250px;
                height: 155px;
              }
            }
            // swiper2-3
            .swiper2-3,.swiper2-4 {
              img {
                width: 129px;
                height: 201px;
                border-right: $swiper2-border
              }
            }
            // .swiper2-5
            .swiper2-5 {
              a {
                width: 129px;
                height: 67px;
                border-bottom: $swiper2-border;
                border-right: $swiper2-border;
                justify-content: center;
                align-items: center;
                img {
                  margin-left: 15px;
                  margin-top: 11px;
                }
              }

            }
            // swiper2-6 
            .swiper2-6 {
              justify-content: space-around;
              align-items: center;
            }
            
          }
        }
      }
    }
    // 广告图
    .banner-ad {
      margin-top: 10px;
      width: 160px;
      height: 510px;
      .banner-ad-1 {
        width: 160px;
        height: 280px;
      }
      p {
        line-height: 24px;
        height: 24px;
        margin-top: 5px;
      }
      .banner-ad-2 {
        width: 160px;
        height: 200px;
      }
    }
    // 底部淘宝头条
    .banner-tbtt {
      position: relative;
      width: 890px;
      height: 98px;
      margin-top: 10px;
      background: #fff;
      display: flex;
      justify-content: space-around;
      align-items: center;
      .more {
        position: absolute;
        z-index: 999;
        right: 11px;
        top: 5px;
        line-height: 30px;
        height: 30px;
        width: 50px;
        text-align: right;
        &:hover {
          color: $color-red;
        }
      }
      .banner-tbtt-left {
        padding-left: 20px;
        width: 160px;
        height: 72px;
        img {
          margin-top: 15px;
        }
        p {
          margin-top: 5px;
          padding-left: 5px;
        }
      }
      .banner-tbtt-right {
        width: 676px;
        height: 72px;
        .swiper-tbtt {
          width: 676px;
          height: 72px;
          .swiper-tbtt-item {
            display: flex;
            justify-content: space-around;
            align-items: center;
            &:hover {
              .swiper-tbtt-item-r p {
                color: $color-red;
                text-decoration: underline;
              }
            }
            img {
              width: 130px;
              height: 70px;
            }
            .swiper-tbtt-item-r {
              width: 500px;
              height: 70px;
              p {
                margin-top: 6px;
                margin-bottom: 10px;
                font-size: 16px;
              }
              span {
                display: block;
                font-size: 14px;
                color: #666;
                line-height: 18px;
                height: 36px;
                overflow: hidden;
              }
            }
          }
        }
      }
      
    }
  }
  // 右侧服务
  .banner-r {
    width: 290px;
    height: 621px;
    margin-top: 10px;
    background: #fff;
    .banner-r-login {
      width: 290px;
      padding-top: 16px;
      background: url('../../assets/image/index/banner-login-bg.png') left top no-repeat;
      background-size: 290px 145px;
      .banner-r-avatar {
        width: 50px;
        height: 50px;
        border-radius: 25px;
        overflow: hidden;
        margin: 0 auto;
      }
      p {
        text-align: center;
      }
      .bar1 {
        display: flex;
        justify-content: center;
        margin-top: 5px;
        a {
          display: inline-block;
          height: 18px;
          line-height: 18px;
          border-radius: 9px;
          overflow: hidden;
          font-size: 12px;
          color:$color-red;
          background: #ffe4db;
          background-image: url('../../assets/image/index/banner-r-icon.png');
          background-repeat: no-repeat;
          padding-left: 20px;
          padding-right: 10px;
        }
        .bar1-btn1 {
          background-position: 0 -527px;
          margin-right: 10px;
        }
        .bar1-btn2 {
          background-position: 0 -572px;
        }
  
      }
      .bar2 {
        display: flex;
        justify-content: center;
        margin-top: 10px;
        a {
          display: inline-block;
          width: 82px;
          line-height: 25px;
          height: 24px;
          color: #fff;
          text-align: center;
          background: $color-orange;
          border-radius: 3px;
          margin: 0 4px;
        }
      }
      .banner-report {
        display: inline-block;
        width: 290px;
        line-height: 26px;
        height: 26px;
        background: #ffe4dd;
        text-align: center;
        position: relative;
        color: $color-red;
        margin-top: 14px;
        i {
          position: absolute;
          right: 55px;
          top: 5px;
          font-size: 16px;
        }
      }
      // tab1
      .bar3 {
        margin-top: 16px;
        border-bottom: 1px solid $background;
        padding-bottom: 10px;
        .bar3-tab {
          display: flex;
          justify-content: center;

          a {
            display: inline-block;
            line-height: 30px;
            height: 30px;
            padding: 0 10px;
            margin: 0 5px;
            border-bottom: 2px solid #fff;
            border-bottom: 2px solid #fff;
            &:hover, &.active {
              border-bottom: 2px solid $color-orange;
              color: $color-orange
            }
          }
        }
        .bar3-items {
          margin-top: 10px;
          .bar3-item {
            width: 262px;
            margin: 0 auto;
            display: flex;
            justify-content: space-between;
            flex-wrap: wrap;
            a {
              display: inline-block;
              width: 120px;
              height: 18px;
              line-height: 18px;
              &:hover {
                color: $color-red;
              }
            }
          }
        }
      }
      // tab2
      .tab2 {
        position: relative;
        .tab2-tabs {
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          a {
            flex: 0 0 25%;
            box-sizing: border-box;
            display: inline-block;
            height: 77px;
            border: {
              top: 1px solid #fff;
              right: 1px solid $background;
              bottom: 1px solid $background;
              left: 1px solid #fff
            }
            
            &:nth-child(4), &:nth-child(8), &:nth-child(12) {
              border-right: 1px solid #fff
            }
            span {
              display: block;
              width: 24px;
              height: 26px;
              margin: 16px auto 8px;
              background-image: url('../../assets/image/index/banner-r-icon.png');
              background-repeat: no-repeat;
            }
            &:nth-child(1) span {
              background-position: 0px 0px;
            }
            &:nth-child(2) span {
              background-position: 0px -87px;
            }
            &:nth-child(3) span {
              background-position: 0px -42px;
            }
            &:nth-child(4) span {
              background-position: 0px -131px;
            }

            &:nth-child(5) span {
              background-position: 0px -174px;
            }
            &:nth-child(6) span {
              background-position: 0px -220px;
            }
            &:nth-child(7) span {
              background-position: 0px -263px;
            }
            &:nth-child(8) span {
              background-position: 0px -307px;
            }

            &:nth-child(9) span {
              background-position: 0px -351px;
            }
            &:nth-child(10) span {
              background-position: 0px -395px;
            }
            &:nth-child(11) span {
              background-position: 0px -439px;
            }
            &:nth-child(12) span {
              background-position: 0px -483px;
            }
            p {
              text-align: center;
            }
            &:hover {
              color: $color-red;
            }
            &.active {
              border-color: $color-red;
              border-bottom-color: #fff;
            }
          }

        }
        .tab2-items {
          position: absolute;
          background: #fff;
          top: 77px;
          left: 0px;
          width: 100%;
          height: 154px;
          border: 1px solid $color-red;
          border-top: none;
          box-sizing: border-box;
          .tab2-item-bar {
            position: absolute;
            top: -1px;
            display: flex;
            justify-content: center;
            width: 100%;
            span {
              display: inline-block;
              flex: 0 0 25%;
              height: 1px;
              background: $color-red;
              &.active {
                background: #fff;
              }
            }
          }
        }
      }
      .banner-r-bottom {
        height: 110px;
        .title {
          font-size: 14px;
          text-align: left;
          padding: 10px 10px 6px;
          // margin: 6px 10px;
          a {
            float: right;
            font-size: 12px;
            &:hover {
              color:$color-red;
            }
          }
        }
        .banner-items {
          display: flex;
          justify-content: space-between;
          flex-wrap: wrap;
          
          .banner-item {
            flex: 0 0 20%;
            margin-top: 5px;
            a {
              display: block;
              background: url('../../assets/image/index/banner-r-bottom.png');
              background-repeat: no-repeat;
              margin: 0 auto;
              width: 32px;
              height: 32px;
              // overflow: hidden;
              position: relative;
              
              .app-qr {
                display: none;
                position: absolute;
                width: 90px;
                height: 110px;
                left: 50%;
                margin-left: -45px;
                top: -120px;
                border: 1px solid #dedede;
                background: #fff;
                p {
                  padding: 10px 0 6px;
                  img {
                    width: 70px;
                    height: 70px;
                  }
                }
                span {
                  display: block;
                  text-align: center;
                }
              }
              &:hover {
                .app-qr {
                  display: block;
                }
              }
            }

            &:nth-child(1) a {
              background-position: 0px 0px;
            }
            &:nth-child(2) a {
              background-position: 0px -38px;
            }
            &:nth-child(3) a {
              background-position: 0px -77px;
            }
            &:nth-child(4) a {
              background-position: 0px -115px;
            }
            &:nth-child(5) a {
              background-position: 0px -153px;
            }

            &:nth-child(6) a {
              background-position: 0px -192px;
            }
            &:nth-child(7) a {
              background-position: 0px -230px;
            }
            &:nth-child(8) a {
              background-position: 0px -268px;
            }
            &:nth-child(9) a {
              background-position: 0px -307px;
            }
            &:nth-child(10) a {
              background-position: 0px -345px;
            }
          }
        }
      }
    }
  }
}
// 动画效果
.leftNav-enter-active {
  animation: leftNav-in .2s;
}
.leftNav-leave-active {
  animation: leftNav-in .2s reverse;
}
@keyframes leftNav-in {
  0% {
    opacity: 0;
    left: 184px;
  }
  100% {
    opacity: 1;
    left: 190px;
  }
}
</style>



