<template>
  <div class="headScrollBar" v-if='winScroll>136'>
    <div class="wrap">
      <router-link to='' class='logo'><img src="../../assets/logo.png" alt=""></router-link>
      <div class="search">
        <div class="search-left">
          <el-dropdown trigger="hover" @command="changeSearch">
            <span class='select-span'>
              {{selectArr[selected].name}}
              <i class="el-icon-arrow-down el-icon--right"></i>
            </span>
            <el-dropdown-menu slot="dropdown">
              <el-dropdown-item v-if="item.id !==selected" v-for='item in selectArr' :key='item.id' :command='item.id'><router-link to=''>{{item.name}}</router-link></el-dropdown-item>
            </el-dropdown-menu>
          </el-dropdown>
        </div>
        <div class="search-input">
          <i class='icon-camera fa fa-camera' @click='upfile'></i>
          <i class='icon-search fa fa-search'></i>
          <input type="text" placeholder="请输入内容">
        </div>
        <div class="search-btn"><span>搜索</span></div>
      </div>
    </div>
    <input type="file" id='upfileFixed'>
    
  </div>
</template>

<script>
import { mapGetters } from 'vuex'
export default {
  name: 'headScrollBar',
  data () {
    return {
      selected: 0,
      selectArr: [
        { id: 0, name: '宝贝' },
        { id: 1, name: '天猫' },
        { id: 2, name: '店铺' }
      ]
    }
  },
  computed: {
    ...mapGetters(['winScroll'])
  },
  created() {
    console.log(this.winScroll)
  },
  mounted() {
    // $(document).on('scroll', () => {
    //   this.winScroll = $(document).scrollTop();
    // })
  },
  methods: {
    changeSearch(id) {
      this.selected = id;
    },
    upfile() {
      document.getElementById('upfileFixed').click();
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '../../assets/css/public.scss';
.headScrollBar {
  position: fixed;
  z-index: 1000;
  top: 0px;
  width: 100%;
  height: 60px;
  background: #fff;
  box-shadow: 0 1px 5px rgba(0,0,0,.1);
  .wrap {
    height: 60px;
    // tupian 
    .logo {
      float: left;
      height: 60px;
      img {
        margin-top: 14px;
        height: 32px;
      }
    }
    // search
    .search {
      float: right;
      margin-right: 317px;
      width: 560px;
      height: 40px;
      margin-top: 10px;
      border-radius: 20px;
      // border: 2px solid $color-orange;
      .search-left {
        float: left;
        .select-span {
          display: inline-block;
          box-sizing: border-box;
          line-height: 36px;
          height: 40px;
          width: 74px;
          background: #f4f4f4;
          padding-left: 20px;
          font-size: 13px;
          cursor: pointer;
          border: 2px solid $color-orange;
          border-right: 0;
          border-radius: 20px 0 0 20px;
          i {
            margin-left: 0px;
          }
        }
      }
      .search-input {
        float: left;
        width: 412px;
        position: relative;
        padding-left: 19px;
        border-top: 2px solid $color-orange;
        border-bottom: 2px solid $color-orange;
        height: 40px;
        box-sizing: border-box;
        i {
          font-size: 16px;
          top: 10px;
        }
        .icon-search {
          position: absolute;
          left: 8px;
          
        }
        .icon-camera {
          position: absolute;
          right: 8px;
          cursor: pointer;
        }
        input {
          margin-top: 2px;
          width: 380px;
          border: 0;
          line-height: 34px;
          height: 34px;
          
        }
      }
    }
    .search-btn {
      float: right;
      width: 74px;
      height: 40px;
      background: $color-orange;
      color: #fff;
      border-radius: 0 20px 20px 0;
      overflow: hidden;
      cursor: pointer;
      span {
        display: inline-block;
        font-size: 16px;
        line-height: 40px;
        margin-left: 18px;
      }

    }
  }
}
#upfileFixed {
  display: none;
}
</style>



