import Vue from 'vue'
import Router from 'vue-router'

// import helloWorld from '@/views/helloWorld'

// import user from '@/views/user'
// import userHome from '@/components/user/userMain/userHome'
// import user1 from '@/components/user/userMain/user1'
// import user2 from '@/components/user/userMain/user2'

import index from '@/views/index'
import login from '@/views/login'
import register from '@/views/register/register'

Vue.use(Router)

// 固定路由
export const constantRouterMap = [
  { path: '/', component: index },
  { path: '/login', component: login },
  { path: '/register', component: register }
]

// 异步路由
export const asyncRouterMap = [
  // 嵌套路由
  // {
  //   path: '/user',
  //   component: user,
  //   children: [
  //     { path: 'userHome', describe: 'userHome', component: userHome },
  //     { path: 'user1', describe: 'user1', component: user1, meta: { role: [1] } },
  //     { path: 'user2', describe: 'user2', component: user2, meta: { role: [2] } }
  //   ]
  // }
]

export default new Router({
  // mode: history,
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
})



