<template>
  <div class="reg1SetName">
    <!-- 表单 -->
    <el-form ref='reg-form' label-width='56px' label-position='right'>
      <el-form-item label='手机号'>
        <el-col :span='8'>
          <div class="tel-ab" @click='telLeftClick'>
            <div class="left">{{telLeft.label}}</div>
            <div class="right">+{{telLeft.value}}</div>
          </div>
          <el-select v-model='telLeft.value' class='telRight' @change='telLeftChange(telLeft.value)' size='small'>
            <el-option
              v-for='item in telLefts'
              :key='item.value'
              :label='item.label'
              :value='item.value'
            >
              <span style='float: left'>{{item.label}}</span>
              <span style='float: right'>+{{item.value}}</span>
            </el-option>
          </el-select>
        </el-col>
        <el-col :span='10'>
          <el-input class='phoneInput' v-model='phone' @change='phoneChange' size='small'></el-input>
          <div v-if='phoneErr.code' :class="phoneErr.code === 1?'phoneMsg red':'phoneMsg green'">
            <i v-if='phoneErr.code === 1' class="fa fa-times-circle" aria-hidden="true"></i>
            <i v-if='phoneErr.code === 2' class="fa fa-check-circle" aria-hidden="true"></i>
            <span>{{phoneErr.msg}}</span>
          </div>
        </el-col>
      </el-form-item>
      <el-form-item label='验证'>
        <drag class='my-drag' />
      </el-form-item>
      <el-form-item>
        <!-- dragState -->
        <el-button @click='submitFn' :class='{ btn: true, on: dragState }'>下一步</el-button>
      </el-form-item>
    </el-form>
    
    <!-- 弹窗 -->
    <el-dialog
      class='reg-dialog'
      title='验证手机'
      :visible.sync='showDialog'
      width='600px'
    >
      <p class='title'><i class="fa fa-info-circle" aria-hidden="true"></i>校验码已发送到您的手机，15分钟内输入有效，请勿泄露</p>
      <el-form class='dialog-form' label-width='66px' label-position='left'>
        <el-form-item label='手机号'>
          {{phone}}
        </el-form-item>
        <el-form-item label='密码'>
          <input type='password' class='dia-password my-input' v-model='password'/>
        </el-form-item>
        <el-form-item label='验证码'>
          <el-col :span='12'>
            <input class='dia-code' v-model='diaCode' @input='changeCode'/>
          </el-col>
          <el-col :offset='1' :span='11'>
            <el-button class='dia-button' size='small' @click='getVerifyCode' :disabled="codeState">获取验证码</el-button>
          </el-col>
          <div class="dia-info">
            <i class="fa fa-check-circle" aria-hidden="true"></i>
            校验码已发送至您的手机，请查收
          </div>
        </el-form-item>
        <el-form-item class='btn-form-item'>
          <div @click.stop='validCode' :class='{"my-button": true, "red": diaState }'>确认</div>
        </el-form-item>
      </el-form>
    </el-dialog>
  </div>
</template>

<script>
import { mapGetters, mapMutations } from 'vuex'
import register from '@/api/register'
import regs from '@/utils/regs'
import drag from '@/components/drag'
export default {
  name: 'login',
  components: {
    drag
  },
  data () {
    return {
      telLeft: {
        label: '中国大陆',
        value: '86'
      },
      telLefts: [
        { label: '中国大陆', value: '86' },
        { label: '中国台湾地区', value: '886' },
        { label: '中国香港', value: '852' },
        { label: '马来西亚', value: '60' },
        { label: '新加坡', value: '65' },
        { label: '日本', value: '81' }
      ],
      phone: '',
      password: '',
      phoneErr: {
        code: 0, // 0 不显示，1错误，2正确
        msg: ''
      },
      // dialog
      interValFun: '',
      count: 60,
      showDialog: false, // 弹窗
      diaState: false, // 确认按钮状态
      codeState: false, // 验证码状态
      diaCode: ''
    }
  },
  computed: {
    ...mapGetters(['dragState'])
  },
  created() {
  },
  mounted() {
    console.log(this.dragState)
    
  },
  methods: {
    ...mapMutations(['SET_REGPHONE', 'SET_REGSTATE']),
    // 1,设置用户名
    telLeftClick() {
      $('.telRight').click()
    },
    telLeftChange(value) {
      let obj = this.telLefts.filter(v => {
        return v.value === value
      })
      console.log(obj)
      this.telLeft.label = obj[0].label
    },
    phoneChange() {
      let phone = this.phone
      if (!phone) {
        this.phoneErr = {
          code: 1,
          msg: '请输入您的手机号码'
        }
      } else if (!regs.phoneReg.test(phone)) {
        this.phoneErr = {
          code: 1,
          msg: '手机号码格式不正确，请重新输入'
        }
      } else {
        this.phoneErr = {
          code: 2,
          msg: ''
        }
      }
    },
    // 提交
    submitFn() {
      if (this.dragState) {
        if (this.phoneErr.code === 2) {
          // next
          clearInterval(this.interValFun)
          let el = $('.dia-button').find('span')

          el.text('获取验证码')
          this.count = 60
          this.diaState = false
          this.codeState = false
          this.diaCode = ''
          this.getVerifyCode()

          this.showDialog = true
        } else {
          $('.phoneInput input').focus()
          if (this.phoneErr.code === 0) {
            this.phoneErr = {
              code: 1,
              msg: '请输入您的手机号码'
            }
          }
        }
      }
      
    },
    // 弹窗
    // 60s倒计时
    countDown() {
      let el = $('.dia-button').find('span')
      if (this.count === 0) {
        this.count = 60
        clearInterval(this.interValFun)

        this.codeState = false
        el.text('重新获取')
      } else {
        this.count--
        let count = this.count
        el.text(count + ' s')
      }
    },
    getVerifyCode() {
      let el = $('.dia-button').find('span')
      if (!this.codeState) {
        this.codeState = true
        this.count--
        let count = this.count
        el.text(count + ' s')
        this.interValFun = setInterval(() => {
          this.countDown()
        }, 1000)
      }
    },
    changeCode() {
      if (this.diaCode) {
        this.diaState = true
      } else {
        this.diaState = false
      }
    },
    // 校验验证码
    validCode(e) {
      let el = e.currentTarget
      if ($(el).hasClass('red')) {
        // 提交数据
        let sendObj = {
          userName: this.phone,
          password: this.password
        }
        register(sendObj).then(res => {
          let msg = res.data.msg
          this.$message({
            type: 'success',
            message: msg
          })
          this.showDialog = false
          this.$router.push(`/login?no=${this.phone}`)
        }).catch(msg => {
          this.$message({
            type: 'error',
            message: msg
          })
        })
        
      }
      
    }
  }
}
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped lang='scss'>
@import '@/assets/css/public.scss';
  .reg1SetName {
    /*下拉框绝对定位*/
    .tel-ab {
      position: absolute;
      left: 5px;
      top: 5px;
      line-height: 30px;
      height: 30px;
      width: 100px;
      z-index: 999;
      background: #fff;
      display: flex;
      justify-content: space-between;
      font-size: 12px;
      cursor: pointer;
      .left {
        flex: auto;
        /*width: 40px;*/
        white-space: nowrap;
        text-overflow: ellipsis;
        overflow: hidden;
        
      }
      .right {
        width: 30px;
        padding-left: 10px;
        text-align: right;
      }
    }
    
    /*验证错误提示信息*/
    .phoneMsg {
      position: absolute;
      right: -100px;
      top: 0px;
      line-height: 40px;
      height: 40px;
      width: 200px;
      font-size: 12px;
      
      i {
        display: inline-block;
        margin-right: 4px;
        font-size: 14px;
  
      }
      &.red {
        color: $color-orange;
      }
      &.green {
        color: rgb(94, 231, 94);
      }
    }
    .my-drag {
      margin-top: 3px;
    }
    /*按钮*/
    .btn {
      width: 160px;
      height: 32px;
      line-height: 32px;
      border-radius: 3px;
      background: #e6e6e6;
      color: #666;
      padding: 0px;
      border: 0px;
      cursor: default;
      &.on {
        background: $color-orange;
        color: #fff;
        cursor: pointer;
      }
    }
    // 弹窗
    .reg-dialog {
      .dialog-form {
        width: 300px;
        margin: 10px auto 0;
        .el-form-item {
          margin-bottom: 0px;
          &.btn-form-item {
            margin-top: 24px;
          }
          // 校验码输入框
          .dia-code {
            border-radius: 3px;
            width: 110px;
            line-height: 30px;
            height: 30px;
            &:focus {
              border-color: $color-border;
            }
          }
          .dia-password {
            border-radius: 3px;
          }
          // 获取校验码按钮
          .dia-button {
            border-color: #c0ccda;
            float: left;
            margin-top: 4px;
            height: 32px;
            border-radius: 3px;
            width: 100%;
          }
          // 提示信息
          .dia-info {
            position: absolute;
            bottom: -30px;
            left: 0px;
            font-size: 12px;
            i {
              color: $color-ok;
              font-size: 14px;
            }
          }
        }
      }
      
      .title {
        i {
          display: inline-block;
          margin-right: 4px;
          color: #00bfff;
          font-size: 16px;
        }
      }
    }
  }
</style>



